*,
*:before,
*:after {
  box-sizing: inherit;
  -webkit-user-select: inherit;
  user-select: inherit;
  -webkit-user-drag: inherit;
  -webkit-tap-highlight-color: inherit;
  backface-visibility: inherit;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Neue Montreal Mono";
  font-weight: normal;
  font-size: 14px;
}
a {
  text-decoration: none;
  color: inherit;
}
ol,
ul {
  list-style: none;
}

@font-face {
  font-family: "Neue Montreal Mono";
  font-weight: 200;
  src: url("./fonts/PPNeueMontrealMono-Thin.woff2") format("woff2");
}
@font-face {
  font-family: "Neue Montreal Mono";
  font-weight: 400;
  src: url("./fonts/PPNeueMontrealMono-Book.woff2") format("woff2");
}
@font-face {
  font-family: "Neue Montreal Mono";
  font-weight: 600;
  src: url("./fonts/PPNeueMontrealMono-SemiBold.woff2") format("woff2");
}

body {
  background-color: rgb(30, 30, 30);
  color: #fff;
  height: 100dvh;
  overflow: hidden;
}
.content {
  background-color: rgb(40, 40, 40);
  margin-left: 200px;
  padding: 20px;
  height: 100dvh;
  /* border-radius: 10px; */
}
